
import XLoading from '@/components/common/xLoading'
import Loading from 'vux/src/components/loading/index.vue'

import Popup from '@/components/common/popup'
import { isEmpty, getStamp } from '../utils/utils'

import { ajaxZixunTicketInfo } from '../api/getData'
var JsBarcode = require('jsbarcode')

export default {
components: {
XLoading,
Loading,
Popup
},
data () {
return {
showXLoading: true, // 显示加载动画
showPopup: false, // 是否显示弹窗
popupType: '', // 是否显示弹窗
popupMsg: '', // 弹窗提示语
popupIcon: 0, // 弹窗icon仅支持0,1,2
code: '', // 二维码
ticket: {} 
}
},
created () {
if (!isEmpty(this.$route.query.code)) {
this.code = this.$route.query.code
} else {
this.$router.go(-1)
}
this.initData()
},
computed: {
},
updated () {
var self = this
self.showBarcode(self.code)
},
methods: {
getStamp (val, type) {
return getStamp(val, type)
},
isEmpty (val) {
return isEmpty(val)
},
showBarcode (val) {
if (val === 0) {
return
}
JsBarcode('#barcode', val, {
format: 'CODE128',
displayValue: true,
fontSize: 24,
lineColor: '#000'
})
},
openPopupFun (type, msg, icon, back) { // 打开弹窗
this.popupType = type
this.popupMsg = msg
this.popupIcon = icon
this.showPopup = true
},
closePopupFun () { // 关闭弹窗
this.showPopup = false
},
beautifyTicketStatus () { // 优化门票状态
var timeNow = parseInt(new Date().getTime() / 1000)
var ticket = this.ticket
var status1 = '已使用'
var status2 = '未使用'
var status3 = '已过期'
if (ticket.is_check === 1) { // 用过
if (ticket.is_through_ticket === 1) { // 通票
if (ticket.rest > 0) { // 还有剩余次数
if (ticket.endtime >= timeNow) { // 未过期
return status2
} else { // 已过期
return status1
}
} else { // 已经使用完
return status1
}
} else { // 单次票
return status1
}
} else { // 未用过
if (ticket.endtime >= timeNow) { // 未过期
return status2
} else { // 已过期
return status3
}
}
},
initData () { // 初始化数据
var self = this
ajaxZixunTicketInfo(self.code).then(response => {
var data = response.data
console.log(data)
if (data.code === 200) {
if (!isEmpty(data.obj.ticket)) {
self.ticket = data.obj.ticket
console.log(self.ticket)
self.code = self.ticket.code
}
} else {
self.openPopupFun('alert', data.msg, 2)
}
self.showXLoading = false
})
}
}
}



